.modal-container {
    width: 400px;
    background-color: aliceblue;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: none;
}

.modal-container .modal-input {
    width:300px;
    margin: 10px;
}

.modal-container .modal-btn {
    margin: 20px;
    width: 300px;
}

.modal-container .modal-words {
    margin-top: 20px;
    font-weight: bold;
}
body{
    margin: 0;
}

.post-link {
    text-decoration: none;
    color: #000;
}

.nav-link {
    text-decoration: none;
    color: #fff;
    padding: 10px;
    background-color: rgb(31, 29, 29);
}

.nav-link:hover{
    background-color: #fff;
    color: #d83535;
    transition: 0.5s ease-in-out all;
}

.navbar-container {
    background-color: rgb(31, 29, 29);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 23px;
    font-weight: bold;
    color: #fff;
    width: 100%;
}


.navbar-right-item {
    padding: 10px;
}


.navbar-right {
    display: flex;
    margin-right: 60px;
}

.navbar-left {
    margin-left: 80px;
    cursor: pointer;
}

.navbar-left:hover {
    color: #d83535;
    transition: 0.5s ease-in-out all;
    background: #e4e1e1;
}

.navbar-right .navbar-right-item {
    cursor: pointer;
}

.navbar-right .navbar-right-item:hover {
    color: #d83535;
    transition: 0.5s ease-in-out all;
    background: #fff;
}

.intro-container {
    background-color: #d8dcdf;
    z-index: -2;
    display: flex;
    justify-content: center;
    padding: 50px;
}

.intro-bg {
    display: flex;
    justify-content: center;
    width: 750px;
}

.intro-pic {
    width: 200px;
    height: 200px;
    background-image: url(../../static/media/thy.7a991349.jpg);
    background-size: cover;
    background-position: center;
    border-radius: 50%;
    box-shadow:  5px 5px 10px #8a8d8f,
