.blog-container-bg {
    background-color: #f5f5f5;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.blog-container {
    display: grid;
    width: 100%;
    grid-template-columns: 20% 60% 20%;
    grid-template-rows:  50px 0px 100%;
    grid-row-gap: 20px;
    background: rgb(255, 255, 255);
}

.blog-title {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
}

.blog-detail {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    text-align: center;
}

.blog-content {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
}

